import Swiper from 'swiper';
import $ from 'jquery';
import gsap from 'gsap';

class LightSwiper {
    constructor() {
        if(document.querySelector(".js-content-slider-wrapper")) {
            this.init();
        }
    }

    init() {
        const self = this;
        window.addEventListener("DOMContentLoaded", self.initLightSwiper, false);
        window.addEventListener("resize", self.initLightSwiper, false);
    }

    initLightSwiper() {
        // if (window.innerWidth <= 992) {
        // 

        const imgSwiperlen = $('.imgmovebottom').length;
        const imgSwiper = new Swiper('.js-img-slider-wrapper', {
            speed: 0,
            fadeEffect: {
              crossFade: true
            },
            autoplay: {
              delay: 5000,
            },
            on: {
              slideChange: function() {
                contentSwiper.slideTo(imgSwiper.activeIndex);
              }
            },
            // direction: 'vertical'
        });
        const contentSwiper = new Swiper(".js-content-slider-wrapper", {
            // slidesPerView: 4,
            fadeEffect: {
              crossFade: true                                                         
            },
            on: {
              slideChange: function() {
                // imgSwiper.slideTo(contentSwiper.activeIndex);
                // var actSlide = parseInt($('.mobileapp-content .swiper-slide-active .mobile-content').attr('data-slide')) + 1;
                var actSlide = parseInt($('.mobileapp-content .swiper-slide').eq(this.activeIndex).find('.mobile-content').attr('data-slide'));
                if(actSlide > imgSwiperlen) {
                  actSlide = 1;
                }
                // alert(actSlide);
                $('.imgmovebottom').removeClass('active');
                $('.imgmovebottom[data-slide="'+ actSlide + '"]').addClass('active');
                gsap.set('.imgmovebottom', {clearProps: 'all', delay: 0.8});
                gsap.to('.imgmovebottom[data-slide="'+ actSlide + '"]', 0.8, {y:0});

                gsap.set('.mobileapp-content .mobile-content > *', {clearProps: 'all', delay: 0.5});
                gsap.to('.mobileapp-content .mobile-content[data-slide="'+ actSlide + '"] > *', 0.6, {
                  y: 0,
                  opacity: 1
                }, 0.2);
              }
            },
            loop: true,
            // direction: 'vertical',
            // effect: 'fade',
            speed: 0,
            observer: true,
            observeParents: true,
            autoplay: false,
            // Responsive breakpoints
            breakpointsInverse: true,
            breakpoints: {
              320: {
                slidesPerView: 1,
                spaceBetween: 0,
                slidesOffsetBefore: 0,
                centeredSlides: false
              },
              370: {
                slidesPerView: 1,
                spaceBetween: 0,
                slidesOffsetBefore: 0,
                centeredSlides: false
              },
              // when window width is >= 600px
              600: {
                slidesPerView: 1,
                spaceBetween: 0,
                centeredSlides: false
              },
              // when window width is >= 320px
              992: {
                slidesPerView: 1,
                spaceBetween: 0,
                centeredSlides: false
              }
            },
            nextButton: '.swiper-button-next',
            prevButton: '.swiper-button-prev',
        });
        // imgSwiper.params.control = contentSwiper;
        // contentSwiper.params.control = imgSwiper;
      }
}

export default LightSwiper;