/*eslint-disable*/
import $ from "jquery"
import gsap from "gsap";
import Swiper from 'swiper';

class AboutUs {
  constructor() {
    if (document.querySelector(".js-about-page")){
     
      this.init();
    }
  }

  init() {


      // breakpoint where swiper will be destroyed
  // and switches to a dual-column layout
  const breakpoint = window.matchMedia( '(min-width:31.25em)' );

  // keep track of swiper instances to destroy later
  let mySwiper;

  //////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////

  const breakpointChecker=()=> {
  
    // if larger viewport and multi-row layout needed
    if ( breakpoint.matches === true ) {

      // clean up old instances and inline styles when available
	  if ( mySwiper !== undefined ) mySwiper.destroy( true, true );

	  // or/and do nothing
	  return;

      // else if a small viewport and single column layout needed
      } else if ( breakpoint.matches === false ) {

        // fire small viewport version of swiper
        return enableSwiper();

      }

  };  
  
  //////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////

  const enableSwiper=()=> {
    
    mySwiper = new Swiper ('.swiper-container.about-team-container', {
      pagination: {
        el: '.our-team-pagination',
        clickable: true,
      },
      // loop: true,
      spaceBetween:15,
      
      slidesPerView: '1.2',
      breakpoints: {
        // when window width is >= 320px
        // 320: {
        //     slidesPerView: 1,
        //     spaceBetween: 30
        // },
        // when window width is >= 480px
        320: {
            slidesPerView: 1.3,
            spaceBetween: 10
        },
        410: {
            slidesPerView: 1.4,
            spaceBetween: 0
        },
        // when window width is >= 640px
        
        }
      // centeredSlides: true,

      // a11y: true,
      // keyboardControl: true,
      // grabCursor: true,

      // pagination


    });

  };

  //////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////
  //////////////////////////////////////////////////////////////////

  // keep an eye on viewport size changes
  breakpoint.addListener(breakpointChecker);

  // kickstart
  breakpointChecker();






















    $(document).ready(function () {

    
        var triggerPosition = 0.5;
    
        var controller = new ScrollMagic.Controller();

        function fadeinup(x,y,d){
            var delay_val = d;
            var anim_elem = x;
            var trig_elem = y;
            var fadeinup1 = gsap.fromTo(anim_elem, 0.5, {y:30,opacity: 0}, {delay: delay_val, y:0,opacity:1,force3D:true, stagger: 0.1});
            new ScrollMagic.Scene({triggerElement: trig_elem,reverse: false, offset: 0, triggerHook: 0.9}).setTween(fadeinup1).addTo(controller);
        }
    
     
        // 
        fadeinup('.js-about-banner > *, .js-about-story .section-heading-lg, .txt-des-bk','.about-banner', 1.5);
        fadeinup('.js-about-vision .section-heading, .js-about-vision .txt-des-bk, .js-about-vision .img-fluid','.about-vision');

        fadeinup('.js-abt-team .section-heading, .js-abt-team .team-member','.abt-teams');

        fadeinup('.js-abt-benefits .section-heading, .js-abt-benefits .txt-des-bk, .js-abt-benefits img, .js-abt-benefits .benifit-list__heading','.abt-benefits');

        fadeinup('.js-abt-join .img-fluid, .js-abt-join .section-heading, .js-abt-join .txt-des-bk, .js-abt-join .link','.about-join');
    });
  }
}

export default AboutUs;
