
import $ from "jquery";


class Login {
    constructor() {
        if (document.querySelector(".js-login-form")) {
            this.init();
        }
    }

    init() {
        const self = this;
        window.addEventListener("DOMContentLoaded", self.initLogin, false);
        window.addEventListener("resize", self.initLogin, false);
    }

    initLogin() {
        $(document).ready(function(){
            $(".icon-pwd").click(function(){
                if($(this).siblings(".input-type").attr('type')=="password"){
                    $(this).siblings(".input-type").attr('type','text')
                }else{
                    $(this).siblings(".input-type").attr('type','password')
                    
                }
            })
            
            $('.forgt-pwd').click(function(){
                $(this.hash).css("display","block")
                $('.login-form-wrp').css("display","none")
            })
            $(".log-item").click(function(){
                
                if($("#login-tab").contents('active')){
                    $(".form-right").css("height","100vh")
                    
                }if ($("#register-tab").contents('active')) {
                    $(".form-right").css("height","100%")
    
                }
            })

           
        })
        
    }
}

export default Login;