/*eslint-disable*/
import $ from "jquery";
import gsap from 'gsap';

class FooterAnim {
  constructor() {
    if (!document.querySelector(".js-home-page")) {
      if (document.querySelector("footer.bg-animationsfooter")) {
        this.init();
      }
    }
  }

  init() {
    var scrollMagicControllerfooter = new ScrollMagic.Controller();

    $(".bg-animationsfooter").each(function() {
      var t4 = new gsap.timeline({}),
        footerExtend = $(this).find(".footer-bg"),
        staggedList = $(this).find(".stagged-list");
        t4.pause();
      t4.fromTo(
        footerExtend,
        1,
        {
          yPercent: 100,
          transformOrigin: "100% 0%",
          opacity: 0,
          ease: "expo.out",
          delay: 2
        },
        { yPercent: 0, opacity: 1, y: 0, ease: "expo.out"},
        0.2
      ).to(
        staggedList,
        0.8,
        { opacity: 1, y: 0, stagger: 0.1, ease: "expo.out"},
        0.1,
        "-=.5"
      );

      var bgAnimationfooter = new ScrollMagic.Scene({
        // triggerElement: '.text',
        triggerElement: this,
        triggerHook: 0.6,
        reverse: false,
        // offset:150
        // offset: 100 /* offset the trigger 150px below #scene's top */
      })
      .on("start", function() {
        t4.play();
      })
        .addTo(scrollMagicControllerfooter);
    });
    
  }
}

export default FooterAnim;