/*eslint-disable*/
import $ from "jquery";
import gsap from 'gsap';

class headerAnim {
  constructor() {
    if (!document.querySelector(".js-home-page")) {
      this.init();
    }

    if (document.querySelector(".js-white-header")) {
      this.headerSticky();
    }
  }

  headerSticky() {
    $(window).scroll(function () {
      if ($(this).scrollTop() > 1) {
        $('header').addClass("sticky");
      }
      else {
        $('header').removeClass("sticky");
      }
    });
  }

  init() {
    $(window).on('load', function () {
      gsap.to($(".page-loader"), 0.5, {
        ease: "power4.out",
        opacity: 0,
        onComplete: function () {
          gsap.to($("header .gsap-logo"), 0.8, {
            ease: "power4.out",
            opacity: 1,
            y: 0
          });
          gsap.to(
            $(".fb-mainnav > li"),
            0.8,
            { ease: "power4.out", delay: 0.2, opacity: 1, y: 0, stagger: 0.2 }
          );
          gsap.to(
            $(".fb-search, .menu-toggle__wrap"),
            0.8,
            { ease: "power4.out", delay: 0.4, opacity: 1, x: 0, stagger: 0.2 }
          );
        }
      });
    });
    
  }
}

export default headerAnim;




