
import $ from "jquery";


class Profile {
    constructor() {
        if (document.querySelector(".js-Profile-form")) {
            this.init();
        }
    }

    init() {
        const self = this;
        window.addEventListener("DOMContentLoaded", self.initProfile, false);
        window.addEventListener("resize", self.initProfile, false);
    }

    initProfile() {
        $(document).ready(function(){
            $("#profile").change(function(){
                $(".img-wrper").find("img").attr('src',$(this).val())
            })
        })
        
    }
}

export default Profile;