import gsap from 'gsap';
import $ from "jquery";
class HamberAnimation {
  constructor() {
    const windowWidth = window.innerWidth;

    this.offset = 0;

    if (document.querySelector(".js-header-animate")) {
      this.init();
    }
  }

  init() {
    const Menutimeline = new gsap.timeline({ paused: true, onReverseComplete:reverseFunction });
    const hamberg = document.querySelector(".js-navtoggler");
    const menu = document.querySelector(".menu");

    function reverseFunction() {
      document.querySelector("header").classList.remove('menu-open');
    }

    Menutimeline.to(menu, .5,
      {
        y: 0,
        ease: "power1.In",
      })

    Menutimeline.from(".menu .menu-nav-list li", 0.3,
      {
        y: -20,
        stagger: .05,
        autoAlpha: 0,
        ease: "power1.InOut"
      });

    Menutimeline.from(".menu .menu-nav-footer-item", 0.3,
      {
        y: -20,
        stagger: .1,
        autoAlpha: 0,
        ease: "power1.InOut"
      },1);

    Menutimeline.from(".menu .fub-social-list__link", 0.3,
      {
        scale: 0,
        stagger: .1,
        autoAlpha: 0,
        ease: "power1.InOut"
      }, 1);

    Menutimeline.reverse();
    hamberg.addEventListener("change", () => {
      document.querySelector("header").classList.add('menu-open');
      Menutimeline.reversed(!Menutimeline.reversed());
      hamberg.classList.toggle("active");
      // console.log(hamberg);
    });
  }

  setOffset() {
    let windowWidth = window.innerWidth;

    if (windowWidth >= 320 && windowWidth < 410) {
      this.offset = 200;
    } else if (windowWidth >= 410 && windowWidth < 767) {
      this.offset = 330;
    } else if (windowWidth >= 767 && windowWidth < 1200) {
      this.offset = 50;
    }
  }
}
export default HamberAnimation;