import gsap from 'gsap';
import $ from 'jquery';
class ProductVibebell {
    constructor() {
      const windowWidth = window.innerWidth;
  
      this.offset = 0;
  
      if (document.querySelector(".js-vibebell-product")) {
        this.init();
      }
    }
  
    init() {
      var vibecontroller = new ScrollMagic.Controller();
      var fadeinup1 = gsap.to('.product-feature__content > *', 0.8, {y:0,opacity:1,force3D:true, stagger: 0.2});
      new ScrollMagic.Scene({triggerElement: '.product-feature-banner', reverse: false,triggerHook: 'onEnter', offset: 250}).setTween(fadeinup1).addTo(vibecontroller);
      // const StoryController = new ScrollMagic.Controller();
      // //Story 1
      // const ProductfeatureTimeline = new gsap.timeline({
      //   defaults: { ease: "power1.out" }
      // });
  
      // //story-line-1-constants
      // const BannerDesc = document.querySelector(".product-feature__content");
    
      // ProductfeatureTimeline
      //   .fromTo(BannerDesc, 
      //     0.5,
      //     {
      //       y: '100%',
      //       autoAlpha: 0,
      //       ease: "power4.out"
      //     },
      //     {
      //       y: '0%',
      //       autoAlpha: 1,
      //       ease: "power4.out"
      //     }
      //   )

      // ProductfeatureTimeline.pause();
      // // Story2Timeline.pause();
      // // Story3Timeline.pause();
  
      // this.setOffset();
  
      // const self = this;

      // window.addEventListener("DOMContentLoaded", self.setOffset, true);
      // window.addEventListener("resize", self.setOffset, true);
  
      // let Story1Scene1 = new ScrollMagic.Scene({
      //   triggerElement: ".product-feature-w",
      //   duration: document.querySelector('.product-feature__content').offsetHeight,
      //   triggerHook: 0.5,
      //   pushFollowers: false,
      //   offset: this.offset
      // }).setTween(
      //   ProductfeatureTimeline
      // ).addTo(StoryController);
      


      $('.prodvibe-anim__welcometext, .prodvibe-anim__unlocktext').append("<div class='row prodvibe-anim__section'></div>");
      $('.prodvibe-anim__welcometext, .prodvibe-anim__unlocktext').prepend("<div class='row prodvibe-anim__section'></div>");


      var unlocklen = $('.prodvibe-anim__unlock .prodvibe-anim__section').length;
      for(var i=0;i<unlocklen;i++) {
        var content = $('.prodvibe-anim__unlock .prodvibe-anim__section').eq(i).find('.product-doorbell-text').html();
        if(content) {
          $('.prodvibe-anim__unlock').find('.content-container-wrap').append("<li>" + content + "</li>");
        }
      }

      var welcomelen = $('.prodvibe-anim__welcome .prodvibe-anim__section').length;
      for(var i=0;i<welcomelen;i++) {
        var content = $('.prodvibe-anim__welcome .prodvibe-anim__section').eq(i).find('.product-doorbell-text').html();
        if(content) {
          $('.prodvibe-anim__welcome').find('.content-container-wrap').append("<li>" + content + "</li>");
        }
      }

      $(window).on('scroll', function(){

        if ($(this).scrollTop() + $(this).outerHeight() > $('.product-feature-banner').offset().top){
          var scrollVal = ($(window).scrollTop() + $(window).outerHeight()) - $('.product-feature-banner').offset().top;
          var scaleMinus = scrollVal/($(window).outerHeight() * 2);
          $(".product-feature-banner .banner-img").css({
            transform: 'translate(-50%, -50%) scale(' + (2 - scaleMinus) + ')'
          });
        }

        if ($(this).scrollTop() + $('.prodpg-header').outerHeight() > $('.prodvibe-anim__unlock').offset().top){
          if(!$('.prodvibe-anim__unlockimg .prodvibe-anim__img').hasClass('sticky')) {
            $('.prodvibe-anim__unlockimg .prodvibe-anim__img').addClass('sticky');
          }

          if (($(this).scrollTop() + $(this).outerHeight()) > ($('.prodvibe-anim__unlock').offset().top + $('.prodvibe-anim__unlock').outerHeight())) {
            $('.prodvibe-anim__unlockimg').addClass('sticky-bottom');
            $('.prodvibe-anim__unlockimg .prodvibe-anim__img').removeClass('sticky');
          }
        }
        
        else {
          $('.prodvibe-anim__unlockimg .prodvibe-anim__img').removeClass('sticky');
          $('.prodvibe-anim__unlockimg').removeClass('sticky-bottom');
        }

        if ($(this).scrollTop() + $('.prodpg-header').outerHeight() > $('.prodvibe-anim__welcome').offset().top){
          if(!$('.prodvibe-anim__welcomeimg .prodvibe-anim__img').hasClass('sticky')) {
            $('.prodvibe-anim__welcomeimg .prodvibe-anim__img').addClass('sticky');
          }

          if (($(this).scrollTop() + $(this).outerHeight()) > ($('.prodvibe-anim__welcome').offset().top + $('.prodvibe-anim__welcome').outerHeight())) {
            $('.prodvibe-anim__welcomeimg').addClass('sticky-bottom');
            $('.prodvibe-anim__welcomeimg .prodvibe-anim__img').removeClass('sticky');
          }
        }
        
        else {
          $('.prodvibe-anim__welcomeimg .prodvibe-anim__img').removeClass('sticky');
          $('.prodvibe-anim__welcomeimg').removeClass('sticky-bottom');
        }


        $('.prodvibe-anim__section').each(function() {
          var this_el = $(this);
          if ($(window).scrollTop() + $('.prodpg-header').outerHeight() > this_el.offset().top){
            var section_index = this_el.index();
            if(this_el.parents('.prodvibe-anim__mainsec').find('.prodvibe-anim__audio').attr('data-status') == 0) {
              var promise = this_el.parents('.prodvibe-anim__mainsec').find('audio')[0].play();
              if (promise !== undefined) {
                promise.then(_ => {
                  this_el.parents('.prodvibe-anim__mainsec').find('.prodvibe-anim__audio').attr('data-status', 1);
                  this_el.parents('.prodvibe-anim__mainsec').find($('.wave-container.coloured-wave-wrap')).addClass('active');
                });
              }                                        
            } 
            if(!this_el.parents('.prodvibe-anim__mainsec').find($('.content-container-wrap li')).eq(section_index).hasClass('active')){
              this_el.parents('.prodvibe-anim__mainsec').find($('.content-container-wrap li')).removeClass('active');
              this_el.parents('.prodvibe-anim__mainsec').find($('.content-container-wrap li')).eq(section_index).addClass('active');
            }
          }
        });
      });
    }
  
    setOffset() {
      let windowWidth = window.innerWidth;
  
      if (windowWidth >= 320 && windowWidth < 410) {
        this.offset = 200;
      } else if (windowWidth >= 410 && windowWidth < 767) {
        this.offset = 330;
      } else if (windowWidth >= 767 && windowWidth < 1200) {
        this.offset = 50;
      }
    }
  }
  
  export default ProductVibebell;  