import Swiper from 'swiper';

class GlimpseSwiper {
    constructor() {
      if(document.querySelector(".js-cs-glimpse-swiper")) {
          this.init();
      }
    }

    init() {
        const self = this;
        window.addEventListener("DOMContentLoaded", self.initGlimpseSwiper, false);
        window.addEventListener("resize", self.initGlimpseSwiper, false);
    }

    initGlimpseSwiper() {
        // if (window.innerWidth <= 992) {
        // }
          const glimpseSwiper = new Swiper(".js-cs-glimpse-swiper", {
            autoHeight: true, //enable auto height
            slidesPerView: 1,
            spaceBetween: 20,
            freeMode: true,
            navigation: {
              nextEl: '.swiper-navigation .nav-next',
              prevEl: '.swiper-navigation .nav-prev',
            },
            breakpoints: {
              640: {
                slidesPerView: 1,
                spaceBetween: 20,
              },
              768: {
                slidesPerView: 1,
                spaceBetween: 40,
              },
              823: {
                slidesPerView: 1,
                spaceBetween: 40,
              },
              992: {
                slidesPerView: "auto",
                spaceBetween: 70,
              },
              1024: {
                slidesPerView: 'auto',
                spaceBetween: 70,
              },
            }
        });
    }
}

export default GlimpseSwiper;
