
import $ from "jquery";
import Swiper from 'swiper';

class TestimonialSwiper {
    constructor() {
        if (document.querySelector(".js-testimonial-swiper")) {
            this.init();
        }
    }

    init() {
        const self = this;
        window.addEventListener("DOMContentLoaded", self.initTestimonialSwiper, false);
        window.addEventListener("resize", self.initTestimonialSwiper, false);
    }

    initTestimonialSwiper() {
        const testimonialSwiper = new Swiper(".js-testimonial-swiper", {
            slidesPerView: 1,
            spaceBetween: 20,
            navigation: {
                nextEl: '.swiper-navigation .nav-next',
                prevEl: '.swiper-navigation .nav-prev',
            }
            // navigation: {
            //   nextEl: '.story-next-bttn',
            //   prevEl: '.story-prev-bttn'
            // }
        });
    }
}

export default TestimonialSwiper;

//animate border
var animPolice, animTime;
animTime = 3000;
animPolice = false;
$('.swiper-navigation .nav').hover(
    function () {
        animPolice = true;
        var borderCircle = $(this).find('.border-circle');
        $(borderCircle).attr('class', 'border-circle filled');
        return setTimeout(function () {
            return animPolice = false;
        }, animTime);
    }
);
$('.swiper-navigation .nav').mouseleave(
    function (e) {
        animPolice = true;
        var borderCircle = $(this).find('.border-circle');
        $(borderCircle).attr('class', 'border-circle');
        return setTimeout(function () {
            return animPolice = false;
        }, animTime);
    }
);


