/*eslint-disable*/
import Swiper from 'swiper';
import $ from 'jquery';
import gsap from 'gsap';

class BannerSwiper {
    constructor() {
      if(document.querySelector(".js-blog-swiper1")) {
          this.init();
      }
    }

    init() {
        const self = this;
        window.addEventListener("DOMContentLoaded", self.initBannerSwiper, false);
        window.addEventListener("resize", self.initBannerSwiper, false);
    }

    initBannerSwiper() {
        // if (window.innerWidth <= 992) {
        // }
          let lenghtSlide =$('.homepage-banner .swiper-slide:not(".swiper-slide-duplicate")').length;
          $(".swiper-pagination-current").text(1);
          $(".swiper-pagination-total").text("/"+lenghtSlide)
          var bannerSwiperlen = $('.homepage-banner__item').length;
          var autoplaytime = 5000;
          const bannerSwiper = new Swiper(".js-blog-swiper1", {
            slidesPerView: 1,
            loop: false,
            // Responsive breakpoints
            breakpointsInverse: true,
            pagination: {
              el: '.swiper-pagination',
              clickable: true,
            },
            autoplay: {
              delay: autoplaytime
            },
            speed: 800,
            loop: true,
            on: {
              slideChangeTransitionEnd: function() {
                $('.homepage-banner')[0].swiper.autoplay.start();
              },
              slideChange: function() {
                $('.homepage-banner').addClass('no-slide');
                var actSlide = parseInt($('.homepage-banner .swiper-slide').eq(this.activeIndex).find('.homepage-banner__item').attr('data-slide'));
                // alert(actSlide)
                gsap.set('.homepage-banner__item .banner-image', {clearProps: 'opacity', delay: 0.5});
                gsap.set('.homepage-banner__item .banner-content__sub-title, .homepage-banner__item .banner-content__des, .homepage-banner__item .banner-content__heading .banner-highlight, .homepage-banner__item .banner-content__heading .banner-hcont', {clearProps: 'all', delay: 0.5});
                gsap.to($('.homepage-banner__item[data-slide="' + actSlide +'"] .banner-image'), 0.8, {delay: 0.2, opacity: 1});
                gsap.to($('.homepage-banner__item[data-slide="' + actSlide +'"] .banner-content__sub-title'), 2, {delay: 0.5, opacity: 1});
                gsap.to($('.homepage-banner__item[data-slide="' + actSlide +'"] .banner-content__des'), 2, {delay: 0.5, opacity: 1});
                gsap.to($('.homepage-banner__item[data-slide="' + actSlide +'"] .banner-content__heading .banner-highlight, .homepage-banner__item[data-slide="' + actSlide +'"] .banner-content__heading .banner-hcont'), 1.6, {ease:'power4.out', delay: 0.5, y: 0, onComplete: function() {
                  $('.homepage-banner').removeClass('no-slide');
                }});
                // gsap.killTweensOf('.homebanner-pagination .progress-bar span');
                // gsap.set('.homebanner-pagination .progress-bar span', {clearProps: 'all'});
                // gsap.to($('.homebanner-pagination .progress-bar span'), (autoplaytime / 1000), {width: '100%'});
                
                $(".swiper-pagination-current").text(actSlide);
                
              }
            }
        });
    }
}

export default BannerSwiper;
