/*eslint-disable*/
import gsap from "gsap";
import $ from "jquery";
import Swiper from "swiper";
import fullpage from "fullpage.js";

class Experience {
  constructor() {
    if (document.querySelector(".js-exp-page")) {
      this.init();
    }
  }

  init() {
    const sliderThumbs = new Swiper('.timeline-list-slider', {
      speed: 1500,
      slidesPerView: 3.2,
      loop: true,
      freeMode: true,
      // loopedSlides: 5, //looped slides should be the same
      watchSlidesVisibility: true,
      watchSlidesProgress: true,
      autoplay: {
        delay: 3600,
        disableOnInteraction: false
      },
      breakpoints: {
        640: {
          slidesPerView: 3,
        },
        768: {
          slidesPerView: 4,
        },
        1024: {
          slidesPerView: 5,
        },
        1200: {
          slidesPerView: 7,
        },
      }
    });

    const expSlider = new Swiper(".experience-tl-slider", {
      // mousewheel: true,
      speed: 1500,
      centeredSlides: true,
      loop: true,
      effect: "fade",
      fadeEffect: {
          crossFade: true 
      },
      thumbs: {
        swiper: sliderThumbs,
      },
      autoplay: {
        delay: 3600,
        disableOnInteraction: false
      },
        on: {
          // slideChangeTransitionEnd: function() {
          //   $('.experience-tl-slider')[0].swiper.autoplay.start();
          // },
          slideChange: function() {
            // $('.experience-tl-slider').addClass('no-slide');
            var actSlide = parseInt($('.experience-tl-slider .swiper-slide').eq(this.activeIndex).find('.experience-tl-slider__item').attr('data-slide'));
            // alert(actSlide)
            // gsap.set('.experience-tl-slider__item .tl-slider-img', {clearProps: 'opacity', delay: 0.5});
            gsap.set('.experience-tl-slider__item .fb-exp-timeline__desc', {clearProps: 'all', delay: 0.5});
            gsap.set('.experience-tl-slider__item .img-block .overlay1', {clearProps: 'all', delay: 0.5});
            gsap.set('.experience-tl-slider__item .img-block .overlay2', {clearProps: 'all', delay: 0.5});
            // gsap.to($('.experience-tl-slider__item[data-slide="' + actSlide +'"] .tl-slider-img'), 0.8, {delay: 0.2, opacity: 1});
            gsap.to($('.experience-tl-slider__item[data-slide="' + actSlide +'"] .img-block .overlay1'), 1, {delay: 0.2, width: 0,
  repeat: 1,
  repeatDelay: 2.5,
  yoyo: true});
            gsap.from($('.experience-tl-slider__item[data-slide="' + actSlide +'"] .fb-exp-timeline__desc'), 1.2, {delay: 0.1,opacity:0,
  repeat: 1,
  repeatDelay: 2.3,
  yoyo: true
});
          }
        }
    });
  }
}
export default Experience;
