import gsap from "gsap";
import $ from "jquery";

class StickyInteractions {
    constructor(from, to) {
      this.from = from;
      this.to = to;
      if (document.querySelector(".js-blog-details")) {
        this.init();
      }
      
     
    }
  
    init() {


      const blogDetailController1 = new ScrollMagic.Controller();
      const heading=document.querySelector('.gsap-bd-heading')
      // const textLR=document.querySelectorAll('.gsap-text-lr')
     
      let headingTimeLine= new gsap.timeline({});
      
      headingTimeLine.fromTo(heading,1,{opacity:0,x:0,y:80},{opacity:1,y:0})

      // textLRTimeLine.fromTo(textLR,.8,{opacity:0,x:-20},{opacity:1,x:0,ease:"Power2.easeIn"})
      const headingScene = new ScrollMagic.Scene({
        triggerElement: ".article__side",
        triggerHook: 0,
      })
      .on("start", function() {
        headingTimeLine.play();
      })
      

        
        $('.gsap-text-lr').each(function(){
                

          // create a scene for each element
                const textLRTimeLine = new gsap.timeline();
                textLRTimeLine.pause();  
                const textLR=$(this);
                textLRTimeLine.fromTo(textLR,.6,{opacity:0,x:-20},{opacity:1,x:0,ease:"Power2.easeOut"},.5)
      
            new ScrollMagic.Scene({
                      triggerElement: this ,
                      triggerHook: .8,
                      // duration:"100%"
                      reverse:true
            
                    })
                    .on("start", function() {
                          textLRTimeLine.play();
                        })
                    // .addIndicators()
            
                    .addTo(blogDetailController1);
                  })
        
      window.addEventListener("DOMContentLoaded", this.addListener());
    }
  
    addListener() {
      // let transDuration = 0;
      const self = this;
      if (window.innerWidth >= 767) {
        if (document.querySelector(".blog-article-block")) {
          window.addEventListener("load", function() {
            let transDuration = document.querySelector(".blog-article-block").offsetHeight - 50;
            const blogDetailController = new ScrollMagic.Controller();
            const Scene = new ScrollMagic.Scene({
              triggerElement: ".js-sticky-transparent",
              triggerHook: 0,

              // pushFollowers: false,
              offset:-50,
              // duration:"100%"
              duration: transDuration
            })
              .setPin(".js-sticky-transparent")
              .addTo(blogDetailController)
              // .addIndicators({
              //   name: "Sticky"
              // });

          });
        }
      }
    }
  }
  
  export default StickyInteractions;