/*eslint-disable*/
import gsap from "gsap";
import $ from "jquery";
class PartnerStory {
  constructor() {
    const windowWidth = window.innerWidth;

    this.offset = 0;

    if (document.querySelector(".js-partner-page")) {
      this.init();
    }
  }

  init() {
    $(".join-form input").blur(function() {
      if ($(this).val()) {
        $(this).addClass("active");
      } else {
        $(this).removeClass("active");
      }
    });

    //Initialize the Story Animations
    const StoryController = new ScrollMagic.Controller();

    //Story 1
    const Story1Timeline = new gsap.timeline({
      defaults: { ease: "power1.out" }
    });
    const Story2Timeline = new gsap.timeline({
      defaults: { ease: "power1.out" }
    });
    const Story3Timeline = new gsap.timeline({
      defaults: { ease: "power1.out" }
    });
    const Story4Timeline = new gsap.timeline({
      defaults: { ease: "power1.out" }
    });
    const Story5Timeline = new gsap.timeline({
      defaults: { ease: "power1.out" }
    });

    //story-line-1-constants
    const BannerTitle = document.querySelector(".partner-banner__desc h1");
    const BannerDesc = document.querySelector(".partner-banner__desc p");
    const BannerOverlay = document.querySelector(
      ".partner-banner__img .banner-overlay"
    );

    //story-line-2-constants
    const AdvantageTitle = document.querySelector(
      ".fb-advantage__heading .heading"
    );
    const AdvantageDesc = document.querySelector(
      ".fb-advantage__heading .desc"
    );
    const FeatureImg = document.querySelectorAll(
      ".fb-advantage__feature-card img"
    );
    const FeatureTitle = document.querySelectorAll(
      ".fb-advantage__feature-card .title"
    );
    const FeatureDesc = document.querySelectorAll(
      ".fb-advantage__feature-card .desc"
    );

    //story-line-3-constants
    const RndTitle = document.querySelector(".rnd__title");
    const RndDesc = document.querySelectorAll(".rnd__desc");
    const RndImg = document.querySelectorAll(".rnd__img-overlay");

    //story-line-4-constants
    const JoinTitle = document.querySelector(".fb-join__title");
    const JoinTab = document.querySelectorAll(".fb-join__tab .nav-item");
    const JoinTabactive = document.querySelectorAll(
      ".fb-join__tab .nav-item.active"
    );

    //story-line-5-constants
    const BannerNumber = document.querySelector(".partner-banner__number");

    Story1Timeline.fromTo(
      BannerOverlay,
      0.5,
      {
        width: "100%"
      },
      {
        width: 0
      }
    )
      .from(BannerTitle, 0.5, {
        yPercent: 80,
        autoAlpha: 0
      })
      .from(
        BannerDesc,
        0.6,
        {
          yPercent: -70,
          autoAlpha: 0
        },
        "<-=1"
      );

    Story2Timeline.from(AdvantageTitle, 0.5, {
      yPercent: -80,
      autoAlpha: 0
    })
      .from(
        AdvantageDesc,
        0.6,
        {
          yPercent: -80,
          autoAlpha: 0
        },
        "<-=1"
      )
      .from(FeatureImg, 0.7, {
        yPercent: -80,
        autoAlpha: 0
      })
      .from(FeatureTitle, 0.5, {
        yPercent: 80,
        autoAlpha: 0
      })
      .from(
        FeatureDesc,
        0.5,
        {
          yPercent: 70,
          autoAlpha: 0
        },
        "<-=1"
      );
    
    Story4Timeline.from(JoinTitle, 0.5, {
      yPercent: 80,
      autoAlpha: 0
    }).fromTo(
      JoinTab,
      0.1,
      {
        x: -10,
        autoAlpha: 0
      },
      {
        x: 0,
        autoAlpha: 0.3,
        stagger: 0.1
      }
    );

    Story5Timeline.from(BannerNumber, 0.8, {
      yPercent: 80,
      autoAlpha: 0
    });

    Story1Timeline.pause();
    Story2Timeline.pause();
    Story3Timeline.pause();
    Story4Timeline.pause();
    Story5Timeline.pause();

    this.setOffset();

    const self = this;

    window.addEventListener("DOMContentLoaded", self.setOffset, true);
    window.addEventListener("resize", self.setOffset, true);

    let Story1Scene1 = new ScrollMagic.Scene({
      triggerElement: ".js-banner",
      duration: "60%",
      pushFollowers: false,
      offset: this.offset
    })
      .on("start", function() {
        Story1Timeline.play();
      })
      .addTo(StoryController);
    // .addIndicators({
    //   name: "banner"
    // });

    let Story1Scene2 = new ScrollMagic.Scene({
      triggerElement: ".fb-advantage",
      duration: "60%",
      pushFollowers: false,
      offset: this.offset,
      triggerHook: 0.5
    })
      .on("start", function() {
        Story2Timeline.play();
      })
      .addTo(StoryController);
    // .addIndicators({
    //   name: "advantage"
    // });

    // R and scene animation for all cards
    $('.rd-scene').each(function(){
      const Story3Timeline = new gsap.timeline({paused: true}),
      RndTitle = this.querySelector(".rnd__title"),
      RndDesc = this.querySelectorAll(".rnd__desc"),
      RndImg = this.querySelectorAll(".rnd__img-overlay");
      Story3Timeline.from(RndTitle, 0.5, {
        yPercent: 80,
        autoAlpha: 0
      })
      .from(RndDesc, 0.5, {
        yPercent: -70,
        autoAlpha: 0,
        stagger: 0.3
      })
      .fromTo(
        RndImg,
        0.8,
        {
          height: "100%"
        },
        {
          height: 0
        },
        "<-=1"
      );

      const Story1Scene3 = new ScrollMagic.Scene({
        triggerElement: this,
        duration: "60%",
        pushFollowers: false,
        offset: this.offset
        // triggerHook: 0.8
      })
      .on("start", function() {
        Story3Timeline.play();
      })
      
      .addTo(StoryController);
      // .addIndicators({
      //   name: "join"
      // });
    
    });

    let Story1Scene4 = new ScrollMagic.Scene({
      triggerElement: ".fb-join",
      duration: "60%",
      pushFollowers: false,
      offset: this.offset
      // triggerHook: 0.8
    })
      .on("start", function() {
        Story4Timeline.play();
      })
      .addTo(StoryController);

    // .addIndicators({
    //   name: "join"
    // });

    let Story1Scene5 = new ScrollMagic.Scene({
      triggerElement: ".partner-banner__number",
      duration: "60%",
      pushFollowers: false,
      offset: this.offset,
      reverse: false,
      triggerHook: 0.8
    })
      .on("start", function() {
        Story5Timeline.play();
        $(".counter-number").each(function() {
          $(this)
            .prop("Counter", 0)
            .animate(
              {
                Counter: $(this).text()
              },
              {
                duration: 3000,
                easing: "swing",
                step: function(now) {
                  $(this).text(Math.ceil(now));
                  // return stop;
                }
              }
            );
        });
        return false;
      })
      .addTo(StoryController);
    // .addIndicators({
    //   name: "number"
    // });
  }

  setOffset() {
    let windowWidth = window.innerWidth;

    if (windowWidth >= 320 && windowWidth < 410) {
      this.offset = 200;
    } else if (windowWidth >= 410 && windowWidth < 767) {
      this.offset = 330;
    } else if (windowWidth >= 767 && windowWidth < 1200) {
      this.offset = 50;
    }
  }
}

export default PartnerStory;
