/*eslint-disable*/
import $ from "jquery";
import Swiper from 'swiper';
import gsap from 'gsap';


class BenefitSlider {
    constructor() {
        if (document.querySelector(".js-product-page")) {
            this.benifitSlider();
        }

        // if (document.querySelector(".js-armour-product")) {
        //     this.init();
        // }
    }

    benifitSlider() {
        var autoplaytime = 5000;
        const benefitSwiper = new Swiper(".js-benefit-slider", {
            slidesPerView: 1,
            loop: false,
            autoplay: false,
            speed: 800,
            allowTouchMove: false,
            on: {
                init: function() {
                    setTimeout(function(){
                        gsap.to($('.swiper-slide:first-of-type .progress-bar span'), (autoplaytime / 1000), {width: '100%', onComplete: function(){
                            $('.js-benefit-slider')[0].swiper.slideNext();
                            $('.js-benefitimg-slider')[0].swiper.slideNext();
                        }});
                    }, 100);
                },
                slideChange: function() { 
                    gsap.killTweensOf('.swiper-slide:not(:nth-of-type(' + (parseInt(this.realIndex) + 1) + ')) .progress-bar span');                   
                    gsap.set('.swiper-slide:not(:nth-of-type(' + (parseInt(this.realIndex) + 1) + ')) .progress-bar span', {clearProps: 'all'});

                    if(this.isEnd) {
                        gsap.to($('.swiper-slide:nth-of-type(' + (parseInt(this.realIndex) + 1) + ') .progress-bar span'), (autoplaytime / 1000), {width: '100%', onComplete: function(){
                            $('.js-benefit-slider')[0].swiper.slideTo(0, 800);
                            $('.js-benefitimg-slider')[0].swiper.slideTo(0, 800);
                        }});
                    }
                    else {
                        gsap.to($('.swiper-slide:nth-of-type(' + (parseInt(this.realIndex) + 1) + ') .progress-bar span'), (autoplaytime / 1000), {width: '100%', onComplete: function(){
                            $('.js-benefit-slider')[0].swiper.slideNext();
                            $('.js-benefitimg-slider')[0].swiper.slideNext();
                        }});
                    }
                },
                touchMove: function(){
                    $('.js-benefitimg-slider')[0].swiper.slideTo((parseInt(this.realIndex) + 1), 800);
                }
            }
        });

        const benefitimgSwiper = new Swiper(".js-benefitimg-slider", {
            slidesPerView: 1,
            loop: false,
            autoplay: false,
            speed: 800,
            on: {
                init: function() {
                    // setTimeout(function(){
                    //     gsap.to($('.swiper-slide:first-of-type .progress-bar span'), (autoplaytime / 1000), {width: '100%', onComplete: function(){
                    //         $('.js-benefit-slider')[0].swiper.slideNext();
                    //     }});
                    // }, 100);
                },
                slideChange: function() {
                    gsap.killTweensOf('.swiper-slide:not(:nth-of-type(' + (parseInt(this.realIndex) + 1) + ')) .progress-bar span');
                    gsap.set('.swiper-slide:not(:nth-of-type(' + (parseInt(this.realIndex) + 1) + ')) .progress-bar span', {clearProps: 'all'});


                    if(this.isEnd) {
                        gsap.to($('.swiper-slide:nth-of-type(' + (parseInt(this.realIndex) + 1) + ') .progress-bar span'), (autoplaytime / 1000), {width: '100%', onComplete: function(){
                            $('.js-benefit-slider')[0].swiper.slideTo(0, 800);
                            $('.js-benefitimg-slider')[0].swiper.slideTo(0, 800);
                        }});
                    }
                    else {
                        gsap.to($('.swiper-slide:nth-of-type(' + (parseInt(this.realIndex) + 1) + ') .progress-bar span'), (autoplaytime / 1000), {width: '100%', onComplete: function(){
                            $('.js-benefit-slider')[0].swiper.slideNext();
                            $('.js-benefitimg-slider')[0].swiper.slideNext();
                        }});
                    }
                },
                touchMove: function() {
                    if(this.swipeDirection == "next") {
                        $('.js-benefit-slider')[0].swiper.slideTo((parseInt(this.realIndex) + 1), 800);
                    }
                    else {
                        $('.js-benefit-slider')[0].swiper.slideTo((parseInt(this.realIndex) - 1), 800);
                    }
                    
                }
            }
        });

        $(document).on('click', '.armourpg-benefits__box', function(){
            var index = $(this).parent().index();
            $('.js-benefit-slider')[0].swiper.slideTo(index, 800);
            $('.js-benefitimg-slider')[0].swiper.slideTo(index, 800);
        });

        $(document).on('click', '.prodpg-header__links', function() {
            $(this).addClass('list-open');
            $(this).parent().append("<div class='list-overlay'></div>");
        });

        $(document).on('click', '.prodpg-header .list-overlay', function() {
            $('.prodpg-header__links').removeClass('list-open');
            $(this).remove();
        });


        var count = 0;
        var index = 0;
        $(document).on('click', '.product-clrlist li', function(){
            $('.product-clrlist').addClass('no-pointer');
            $('.product-clrlist li').removeClass('active');
            $(this).addClass('active');
            var target = $(this).attr('id');
            index = $('.prod-color-wrapper img[data-img="' + target +'"]').index();
            if(index > count) {
                $('.prod-color-wrapper img[data-img="' + target +'"]' ).addClass('active');
                $('.product-namelist li:not([data-value="' + target +'"])').removeClass('active');
                $('.product-namelist li[data-value="' + target +'"]').addClass('active');
                count = index;
                setTimeout(function(){
                    $('.product-clrlist').removeClass('no-pointer');
                    $('.prod-color-wrapper img:not([data-img="' + target +'"])').removeClass('active');
                }, 500);
            }
            else {
                $('.product-clrlist').removeClass('no-pointer');
                $('.prod-color-wrapper img[data-img="' + target +'"]' ).addClass('active');
                $('.prod-color-wrapper img:not([data-img="' + target +'"])').removeClass('active');
                $('.product-namelist li:not([data-value="' + target +'"])').removeClass('active');
                $('.product-namelist li[data-value="' + target +'"]').addClass('active');
                count = index;
            }
        });
    // }

    // init() {
        $('.armourpg-features__img[data-slide="0"]').addClass('active').css("transform","translate(0px, 0px)");
        const delayTime = 5;
        const featcontSlider = new Swiper('.js-armour-feature-slider', {
            speed: 0,
            slidesPerView: 1,
            fadeEffect: {
                 crossFade: true 
            },
            allowTouchMove: false,
            effect: "fade",
            on: {
                init: function() {
                    $('.js-armour-feature-slider')[0].swiper.autoplay.stop();
                    setTimeout(function(){
                        var staggerVal = 0.2;
                        gsap.to('.armourpg-features__text[data-slide="0"] > *', 0.5, {
                            y: 0,
                            opacity: 1,
                            stagger: staggerVal,
                            repeat: 1,
                            delay: 0.5,
                            repeatDelay: delayTime,
                            yoyo: true,
                            onComplete: function () {
                                $('.js-armour-feature-slider')[0].swiper.slideNext();
                            }
                        });
                    }, 500)
                },
                slideChange: function() {
                    if(this.isEnd) {
                        var staggerVal = 0.2;
                        gsap.to('.armourpg-features__text[data-slide="'+ this.realIndex + '"] > *', 0.5, {
                            y: 0,
                            opacity: 1,
                            stagger: staggerVal,
                            repeat: 1,
                            delay: 0.5,
                            repeatDelay: delayTime,
                            yoyo: true,
                            onComplete: function () {
                                staggerVal = -2;
                                $('.js-armour-feature-slider')[0].swiper.slideTo(0, 800);
                            }
                        });
                    }
                    else {
                        var staggerVal = 0.2;
                        gsap.to('.armourpg-features__text[data-slide="'+ this.realIndex + '"] > *', 0.5, {
                            y: 0,
                            opacity: 1,
                            delay: 0.5,
                            stagger: staggerVal,
                            repeat: 1,
                            repeatDelay: delayTime,
                            yoyo: true,
                            onComplete: function () {
                                staggerVal = -2;
                                $('.js-armour-feature-slider')[0].swiper.slideNext();
                            }
                        });
                    }

                    $('.armourpg-features__img').removeClass('active');
                    $('.armourpg-features__img[data-slide="'+ this.realIndex + '"]').addClass('active');
                    gsap.set('.armourpg-features__img', {clearProps: 'all', delay: 0.8});
                    gsap.to('.armourpg-features__img[data-slide="'+ this.realIndex + '"]', 0.8, {y:0});
                }
            }
        });
    }
}

export default BenefitSlider;