/*eslint-disable*/
import $ from "jquery"
import gsap from "gsap";

class CustomerStories {
    constructor() {
        if (document.querySelector(".js-cstories-page")) {
            this.init();
        }
    }

    init() {
        $(document).ready(function () {
            var controller = new ScrollMagic.Controller();

            function fadeinup(x, y) {
                var anim_elem = x;
                var trig_elem = y;
                var fadeinup1 = gsap.fromTo(anim_elem, 0.8, {
                    y: 30,
                    opacity: 0
                }, {
                    y: 0,
                    opacity: 1,
                    force3D: true,
                    stagger: 0.2
                });
                new ScrollMagic.Scene({
                    triggerElement: trig_elem,
                    reverse: false,
                    offset: 100,
                    triggerHook: 0.9
                }).setTween(fadeinup1).addTo(controller);
            }

            function imgAppear(x) {
                var imgC1 = $(x).find(".cs-use__img--1");
                var imgC2 = $(x).find(".cs-use__img--2");
                var imgC3 = $(x).find(".cs-use__img--3");
                var img1 = $(x).find(".cs-use__img--1").find("img");
                var img2 = $(x).find(".cs-use__img--2").find("img");
                var img3 = $(x).find(".cs-use__img--3").find("img");
                var trig_elem = $(x).find(".row");
                var imgTl = new gsap.timeline({
                    delay: 0.3
                });

                imgTl.fromTo(imgC1, 0.4, {
                        y: 30,
                        opacity: 0
                    }, {
                        y: 0,
                        opacity: 1,
                        force3D: true
                    })
                    .fromTo(img1, 0.4, {
                        y: 30,
                        opacity: 0
                    }, {
                        y: 0,
                        opacity: 1,
                        force3D: true
                    }, "-=0.2")
                    .fromTo(imgC2, 0.4, {
                        y: 30,
                        opacity: 0
                    }, {
                        y: 0,
                        opacity: 1,
                        force3D: true
                    }, "-=0.2")
                    .fromTo(img2, 0.4, {
                        y: 30,
                        opacity: 0
                    }, {
                        y: 0,
                        opacity: 1,
                        force3D: true
                    }, "-=0.2")
                    .fromTo(imgC3, 0.4, {
                        y: 30,
                        opacity: 0
                    }, {
                        y: 0,
                        opacity: 1,
                        force3D: true
                    }, "-=0.2")
                    .fromTo(img3, 0.4, {
                        y: 30,
                        opacity: 0
                    }, {
                        y: 0,
                        opacity: 1,
                        force3D: true
                    }, "-=0.2")

                new ScrollMagic.Scene({
                    triggerElement: x,
                    reverse: false,
                    offset: 0,
                    triggerHook: 0.9
                }).setTween(imgTl).addTo(controller);
            }

            function boxAppearBg(x, y) {
                var bg = $(y).find(".animate-bg");
                var trig_elem = y;
                var boxTl = new gsap.timeline({
                    delay: 0.3
                });

                boxTl.fromTo(bg, 0.9, {
                        opacity: 1
                    }, {
                        height: 0,
                        force3D: true
                    })
                    .fromTo(x, 0.4, {
                        y: 30,
                        opacity: 0
                    }, {
                        y: 0,
                        opacity: 1,
                        force3D: true,
                        stagger: 0.1
                    }, "-=0.1")

                new ScrollMagic.Scene({
                    triggerElement: trig_elem,
                    reverse: false,
                    offset: 0,
                    triggerHook: 0.9
                }).setTween(boxTl).addTo(controller);
            }

            function sideAppear(x, y, z) {
                var sideTl = new gsap.timeline({
                    delay: 0.3
                });

                sideTl.fromTo(x, 0.6, {
                        x: -30,
                        opacity: 0
                    }, {
                        x: 0,
                        opacity: 1,
                        force3D: true
                    })
                    .fromTo(y, 0.6, {
                        x: 30,
                        opacity: 0
                    }, {
                        x: 0,
                        opacity: 1,
                        force3D: true,
                        stagger: 0.6
                    }, "-=0.6")

                new ScrollMagic.Scene({
                    triggerElement: z,
                    reverse: false,
                    offset: 0,
                    triggerHook: 0.9
                }).setTween(sideTl).addTo(controller);
            }


            fadeinup('.cs-banner .col-12 > *', '.cs-banner');

            fadeinup('.cs-why-ha .cs-why-ha__heading, .cs-why-ha h5,.cs-why-ha .vid-container', '.cs-why-ha');
            fadeinup('.cs-why-ha .cs-why-ha__list li', '.cs-why-ha__list');

            fadeinup('.cs-why-fueb .section-heading,.cs-why-fueb__desc,.cs-why-fueb .vid-container', '.cs-why-fueb');

            fadeinup('.cs-glimpse .section-heading,.cs-glimpse__tag', '.cs-glimpse');
            fadeinup('.cs-glimpse .js-cs-glimpse-swiper', '.cs-glimpse .js-cs-glimpse-swiper');

            fadeinup('.cs-impact .section-heading,.cs-impact__desc,.cs-impact .vid-container', '.cs-impact');
            fadeinup('.cs-impact .cs-impact-list li', '.cs-impact .cs-impact-list');

            imgAppear('.cs-use');

            boxAppearBg('.cs-story .section-heading,.cs-story__desc,.cs-story .link', '.cs-story')



            //animate border
            var animPolice, animTime;
            animTime = 3000;
            animPolice = false;
            $('.swiper-navigation .nav').hover(
                function () {
                    animPolice = true;
                    var borderCircle = $(this).find('.border-circle');
                    $(borderCircle).attr('class', 'border-circle filled');
                    return setTimeout(function () {
                        return animPolice = false;
                    }, animTime);
                }
            );
            $('.swiper-navigation .nav').mouseleave(
                function (e) {
                    animPolice = true;
                    var borderCircle = $(this).find('.border-circle');
                    $(borderCircle).attr('class', 'border-circle');
                    return setTimeout(function () {
                        return animPolice = false;
                    }, animTime);
                }
            );


            //contact us js
            fadeinup('.js-contact-1 .img-fluid,.js-contact-1 .section-heading,.js-contact-1 .txt-des-bk,.js-contact-1 .link', '.js-contact-1');
            fadeinup('.js-contact-2 .img-fluid,.js-contact-2 .section-heading,.js-contact-2 .contact-form', '.js-contact-2');

            fadeinup(".js-contact-3 .img-fluid", ".js-contact-3 .collaborate-heading,.js-contact-3 .link", ".js-contact-3");

            boxAppearBg(".js-contact-4 .location-block,.js-contact-4 .section-heading", ".js-contact-4");
        });
    }
}

export default CustomerStories;
