import gsap from "gsap";
import $ from "jquery";

class ForumPost {
  constructor() {
    if (document.querySelector(".js-forum-page")) {
      this.init();
    }
  }

  init() {
    const profileMenu = document.querySelector(".profile-menu a");
    const profileDropdown = document.querySelector(".profile-menu__dropdown");
    const profileArrow = document.querySelector(".arrow-menu");

    profileMenu.addEventListener("click", function(){
      profileDropdown.classList.toggle("active");
      profileArrow.classList.toggle("active");

    });
  }
}

export default ForumPost;