
import $ from "jquery";


class Locaters {
    constructor() {
        if (document.querySelector(".js-locate")) {
            this.init();
        }
    }

    init() {
        const self = this;
        window.addEventListener("DOMContentLoaded", self.initLocate, false);
        window.addEventListener("resize", self.initLocate, false);
    }

    initLocate() {
        $('.lacate-search').keypress(function(){
            // console.log($(this).val());
            if($(this).val() != ' '){
                $('.close-btn').css("opacity","1");
                $('.locate-search').css("opacity","0");
            }
           
        })
        //close button
        $('.close-btn').click(function(){
            $('.lacate-search').val(' ');
            $('.close-btn').css("opacity","0");
            $('.locate-search').css("opacity","1");

        })
        
    }
}

export default Locaters;