/*eslint-disable*/
import $ from "jquery"
import gsap from "gsap";

class CustomerListing {
  constructor() {
    if (document.querySelector(".js-clisting-page")) {
      this.init();
    }
  }

  init() {
    $(document).ready(function () {
        var $img1 = $("#cl-banner .pos-1"),
            $img2 = $("#cl-banner .pos-2"),
            $img3 = $("#cl-banner .pos-3"),
            $img4 = $("#cl-banner .pos-4"),
            $img5 = $("#cl-banner .pos-5"),
            tl,
            t2;
    
        tl = new gsap.timeline({ delay: 0.5 });
    
        tl.to($img1, 0.8, { scale: 1, opacity: 1 })
            .to($img2, 0.8, { scale: 1, opacity: 1 },"-=0.6")
            .to($img3, 0.8, { scale: 1, opacity: 1 },"-=0.6")
            .to($img4, 0.8, { scale: 1, opacity: 1 },"-=0.6")
            .to($img5, 0.8, { scale: 1, opacity: 1 },"-=0.6");
    
        function t2Start() {
            t2 = new gsap.timeline({ delay: 0.5 });
            t2.kill();
            t2.fromTo($img1, 0.75, { scale: 1, }, { scale: 1.1, repeat: -1, yoyo: true })
                .fromTo($img2, 0.75, { scale: 1, }, { scale: 1.1, repeat: -1, yoyo: true })
                .fromTo($img3, 0.75, { scale: 1, }, { scale: 1.1, repeat: -1, yoyo: true })
                .fromTo($img4, 0.75, { scale: 1, }, { scale: 1.1, repeat: -1, yoyo: true })
                .fromTo($img5, 0.75, { scale: 1, }, { scale: 1.1, repeat: -1, yoyo: true });
            t2.restart();
        }
    
        var triggerPosition = 0.5;
    
        var controller = new ScrollMagic.Controller();
    
        function boxAppear(x) {
            var text = $(x).find(".cl-review");
            var imgC = $(x).find(".cl-review-box__img-c");
            var trig_elem = x;
            var boxTl = new gsap.timeline({ delay: 0.3 });
    
            boxTl.fromTo(text, 0.4, {y:30,opacity: 0}, {y:0,opacity:1,force3D:true})
                .fromTo(imgC, 0.4, {y:30,opacity: 0}, {y:0,opacity:1,force3D:true},"-=0.2")
    
            new ScrollMagic.Scene({ triggerElement: trig_elem, reverse: false, offset: 0, triggerHook: 0.9}).setTween(boxTl).addTo(controller);
        }
    
        function boxAppearBg(x,y) {
            var bg = $(y).find(".animate-bg");
            var trig_elem = y;
            var boxTl = new gsap.timeline({ delay: 0.3 });
    
            boxTl.fromTo(bg, 0.6, {opacity: 1}, {height:0,opacity:0,force3D:true})
                .fromTo(x, 0.4, {y:30,opacity: 0}, {y:0,opacity:1,force3D:true, stagger: 0.1}, "-=0.1")
    
            new ScrollMagic.Scene({ triggerElement: trig_elem, reverse: false, offset: 0, triggerHook: 0.9}).setTween(boxTl).addTo(controller);
        }
    
        function fadeinup(x,y){
            var anim_elem = x;
            var trig_elem = y;
            var fadeinup1 = gsap.fromTo(anim_elem, 0.8, {y:30,opacity: 0}, {y:0,opacity:1,force3D:true, stagger: 0.2});
            new ScrollMagic.Scene({triggerElement: trig_elem,reverse: false, offset: 0, triggerHook: 0.9}).setTween(fadeinup1).addTo(controller);
        }
    
        boxAppear(".js-cl-review-1");
        boxAppear(".js-cl-review-2");
        boxAppear(".js-cl-review-3");
        boxAppear(".js-cl-review-4");
        boxAppear(".js-cl-review-5");
        boxAppear(".js-cl-review-6");
    
        fadeinup('.cl-banner-heading','.cl-banner');
        boxAppearBg('.cl-black-box__title ,.cl-black-box .link','.cl-black-box')
        //smooth scrolling 
        $('.customer-listing-img').click(function(e){
            e.preventDefault();
            var new_position = $(this.hash).offset();
            $('html, body').stop().animate({ scrollTop: new_position.top }, 500);
        });
    });
  }
}

export default CustomerListing;

