import gsap from "gsap";
import $ from "jquery";

class careerButton {

    constructor() {

        // alert(1)
        if (document.querySelector(".js-careerDetail-page")) {
          this.init();
        }
      }
    
      init() {
          $(document).ready(function(){
              $('.resume-input').change(function(){
                  $('.resume-return-text').text($(this).val())

              })

          })
          $('.apply-job-btn').click(function(){
           let ele = $(this).attr('data-ref')
            var new_position = $("."+ele).offset();
            $('html, body').stop().animate({ scrollTop: new_position.top }, 500);
        });
        
      }
    


}

export default careerButton;