import $ from "jquery";
import Swiper from 'swiper';

class BlogSwiper {
    constructor() {
        if(document.querySelector(".js-blog-swiper")) {
            this.init();
        }
    }

    init() {
        const self = this;
        window.addEventListener("DOMContentLoaded", self.initBlogSwiper, false);
        window.addEventListener("resize", self.initBlogSwiper, false);
    }

    initBlogSwiper() {
        // if (window.innerWidth <= 992) {
        // }
          const blogSwiper = new Swiper(".js-blog-swiper", {
            slidesPerView: 1.2,
            // centeredSlides: true,
            // centeredSlidesBounds: true,
            spaceBetween: 30,
            loop: false,
            // loopedSlides: 10,
            
            // Responsive breakpoints
            // breakpointsInverse: true,
            // 
            navigation: {
                nextEl: '.swiper-navigation .nav-next',
                prevEl: '.swiper-navigation .nav-prev',
            },
            
            breakpoints: {
                768: {
                    slidesPerView: 2,
                    spaceBetween: 30
                },
                1024: {
                    slidesPerView: 2.5,
                    spaceBetween: 30,
                    // slidesPerGroup: 4
                },
                // when window width is >= 1400px
                1204: {
                    slidesPerView: 3.2,
                    spaceBetween: 30
                },
                1500: {
                    slidesPerView: 3.5,
                    spaceBetween: 30
                }
            }
        });
    }
}



export default BlogSwiper;

//animate border
var animPolice, animTime;
animTime = 3000;
animPolice = false;
$('.swiper-navigation .nav').hover(
    function () {
        animPolice = true;
        var borderCircle = $(this).find('.border-circle');
        $(borderCircle).attr('class', 'border-circle filled');
        return setTimeout(function () {
            return animPolice = false;
        }, animTime);
    }
);
$('.swiper-navigation .nav').mouseleave(
    function (e) {
        animPolice = true;
        var borderCircle = $(this).find('.border-circle');
        $(borderCircle).attr('class', 'border-circle');
        return setTimeout(function () {
            return animPolice = false;
        }, animTime);
    }
);